<template>
  <div class="container">
    <div class="camera">
      <video ref="video" autoplay></video>
      <canvas ref="canvas" style="display:none;"></canvas>
    </div>
    <div class="controls">
      <input type="file" ref="fileInput" @change="handleFileChange">
      <button @click="captureImage" :disabled="processing">
        <span >{{ processing ? 'Thinking...' :'Capture' }}</span>
      </button>
    </div>
    <div class="output">
      <div class="transcript">{{ transcript }}</div>
      <audio controls :src="audioSrc" ref="audio"></audio>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      transcript: '',
      audioSrc: '',
      processing: false
    };
  },
  mounted() {
    this.startCamera();
  },
  methods: {
    async startCamera() {
      const constraints = {
        video: true
      };
      try {
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        this.$refs.video.srcObject = stream;
      } catch (err) {
        console.error('Error accessing camera: ', err);
      }
    },
     handleFileChange() {
      const file = this.$refs.fileInput.files[0];
      if (file) {
        this.processing = true;
        const formData = new FormData();
        formData.append('file', file);
        this.sendImage(formData);
      }
    },
    captureImage() {
      this.processing = true;
      this.$refs.canvas.width = this.$refs.video.videoWidth;
      this.$refs.canvas.height = this.$refs.video.videoHeight;
      this.$refs.canvas.getContext('2d').drawImage(this.$refs.video, 0, 0);
      this.$refs.canvas.toBlob((blob) => {
        const formData = new FormData();
        formData.append('file', blob, 'image.jpeg');
        this.sendImage(formData);
      }, 'image/jpeg');
    },
    async sendImage(formData) {
      // Send formData to server and get response
      try {
        const response = await fetch('https://oyzekbotvh.execute-api.ap-northeast-1.amazonaws.com/prod/app/narration/123487', {
          method: 'POST',
          body: formData
        });
        const data = await response.json();
        console.log(data);
        this.transcript = data.response.transcript;
        this.audioSrc = data.response.audio_file;
        this.$nextTick(() => {
          this.$refs.audio.play();
                    this.processing = false;
        });      
      } catch (err) {
        console.error('Error sending image to server: ', err);
                  this.processing = false;

      }
    }
  }
};
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.camera {
  position: relative;
  margin-bottom: 20px;
}

.controls {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.controls input[type="file"] {
  margin-bottom: 10px;
}

.controls button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.controls button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.controls button span {
  display: inline-block;
}


.controls button:disabled span:first-child {
  display: inline-block;
}

.output {
  text-align: center;
}

.transcript {
  font-size: 18px;
  margin-bottom: 10px;
}

